import { Modal, Form, Input } from 'antd';
import * as React from 'react';
import { formItemLayout } from '@/constants';
import { FormComponentProps } from 'antd/lib/form';
import { IAddProject } from '@/api';
import { toastformError } from '@/utils';
import { connect } from 'react-redux';
import { IAction } from '@/types';
import { bindActionCreators, Dispatch } from 'redux';
import { doSignup } from '@/store/actions';
interface Props extends FormComponentProps {
  visible: boolean;
  onClose: (param: boolean) => any;
  handleAddUser: (param: IAddProject) => IAction;
}

const UserAddModel = (props: Props) => {
  const { getFieldDecorator } = props.form;

  React.useEffect(() => {
    props.form.resetFields();
  }, [props.visible]);

  const handleSubmit = (e: React.MouseEvent) => {
    e.preventDefault();
    props.form.validateFields((err, values) => {
      if (err) {
        toastformError(err);
        return;
      }
      props.handleAddUser(values);
      props.onClose(false);
    });
  };

  return (
    <Modal onOk={handleSubmit} title='' visible={props.visible} onCancel={() => props.onClose(false)}>
      <Form layout='horizontal' {...formItemLayout}>
        <Form.Item label=''>
          {getFieldDecorator('username', {
            rules: [{ required: true, message: '' }]
          })(<Input placeholder='' />)}
        </Form.Item>
        <Form.Item label=''>
          {getFieldDecorator('nickname', {
            rules: [{ required: true, message: '' }]
          })(<Input placeholder='' />)}
        </Form.Item>
        <Form.Item label='email'>
          {getFieldDecorator('email', {})(<Input placeholder='Email' />)}
        </Form.Item>
        <Form.Item label=''>
          {getFieldDecorator('mobile', {})(<Input placeholder='' />)}
        </Form.Item>
        <Form.Item label=''>
          {getFieldDecorator('password', {})(<Input placeholder='' />)}
        </Form.Item>
      </Form>
    </Modal>
  );
};

const mapDispatchToProps = (dispatch: Dispatch<IAction>) =>
  bindActionCreators(
    {
      handleAddUser: params => {
        return doSignup.request(params);
      }
    },
    dispatch
  );

export default connect(null, mapDispatchToProps)(Form.create<Props>()(UserAddModel));
